.s2 {
  padding: 127px 0 86px;
  background-color: #ffffff;

  .preform-cards {
    display: flex;
    flex-direction: row;
    color: #F3914E;
    justify-content: space-around;
    margin-bottom: 46px;
  }

  svg {
    fill: #F3914E;
  }

  .preform-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    p {
      @extend %stsBold;
      font-size: 15px;
      line-height: 20px;
      letter-spacing: 0.38px;
    }
  }

  .svg-container {
    height: 68px;
    margin-bottom: 17px;
    display: flex;
    align-items: flex-end;
  }

  $svg-width: 162px,
  65px,
  130px,
  87px;
  @for $i from 1 through length($svg-width) {
    .s2-svg-#{$i} {
      width: nth($svg-width, $i);
    }
  }

  $svg-height: 54px,
  63px,
  58px,
  68px;
  @for $i from 1 through length($svg-height) {
    .s2-svg-#{$i} {
      height: nth($svg-height, $i);
    }
  }

  .content-txt {
    @extend %sdReg;
    font-size: 15px;
    line-height: 29px;
    letter-spacing: 0.38px;
    color: #525252;
    text-align: center;
  }
}



@media screen and (max-width: 1259px) and (min-width: 640px) {

  .s2 {
    padding: 64px 0 72px;

    .container {
      padding: 0 50px;
    }

    .preform-cards {
      flex-direction: column;
      flex-wrap: wrap;
      height: 253px;
      margin-bottom: 53px;
    }

    .preform-card {
      &:nth-child(1) {
        order: 1;
      }
      &:nth-child(3) {
        order: 2;
      }
      &:nth-child(2) {
        order: 3;
      }
      &:nth-child(4) {
        order: 4;
      }

      p {
        font-size: 14px;
      }
    }

    .svg-container {
      margin-bottom: 13px;
    }

    $svg-width: 144px,
    50px,
    108px,
    62px;
    @for $i from 1 through length($svg-width) {
      .s2-svg-#{$i} {
        width: nth($svg-width, $i);
      }
    }

    $svg-height: 48px,
    48px,
    48px,
    48px;
    @for $i from 1 through length($svg-height) {
      .s2-svg-#{$i} {
        height: nth($svg-height, $i);
      }
    }

    .content-txt {
      font-size: 14px;
      line-height: 26px;
      letter-spacing: 0.35px;
    }
  }
}


@media screen and (max-width: 639px) {

  .s2 {
    padding: 65px 0 18px;

    .container {
      padding: 0 21px;
      max-width: 527px;
      margin: 0 auto;
    }

    .preform-cards {
      flex-direction: column;
      flex-wrap: wrap;
      height: 279px;
      align-items: center;

      justify-content: flex-start;
      margin-bottom: 0;
    }

    .preform-card {
      justify-content: flex-start;
      min-height: 134px;

      &:nth-child(1) {
        order: 1;
      }
      &:nth-child(3) {
        order: 2;
      }
      &:nth-child(2) {
        order: 3;
      }
      &:nth-child(4) {
        order: 4;
      }

      p {
        font-size: 13px;
        line-height: 16px;
      }
    }

    .svg-container {
      height: 42px;
      margin-bottom: 13px;
    }

    $svg-width: 108px,
    38px,
    81px,
    46px;
    @for $i from 1 through length($svg-width) {
      .s2-svg-#{$i} {
        width: nth($svg-width, $i);
      }
    }

    $svg-height: 36px,
    36px,
    36px,
    36px;
    @for $i from 1 through length($svg-height) {
      .s2-svg-#{$i} {
        height: nth($svg-height, $i);
      }
    }

    .content-txt {
      font-size: 13px;
      line-height: 21px;
      letter-spacing: 0.33px;
    }
  }
}